.footer {
  padding: 40px 0;
  background-color: #252527;
  color: white;
  @media (max-width: 767px) {
    padding: 20px 0;
  }

  &__inner{
    text-align: center;
  }
  &__social-networks{
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    max-width: 470px;
  }
  &__btn{
    margin: 10px ;
  }
  &__copyright{
    margin-top: 30px;
  }
}